.header { background-color: #25282a; color: #fff; padding: 25px 0 18px; }

.header__top { display: flex; align-items: center; justify-content: space-between; color: #fff; font-size: 14px; line-height: 17px; font-weight: 100; }

.header__logo { display: flex; }

.header__logo > a { display: flex; width: 95px; margin-right: 15px; }

.header__logo > span { display: block; width: 200px; }

.header .language > a { color: #979797; }

.header .language > a:first-child::after { content: '/'; display: inline-block; font-size: 18px; line-height: 22px; color: #fff; padding-left: 6px; }

.header .language > a.active { color: #fff; font-size: 18px; line-height: 22px; }

.header__address { color: #979797; margin: 0; }

.phone { position: relative; padding-left: 10px; }

.phone__list { display: none; width: 100%; padding: 10px; position: absolute; left: 0; right: 0; top: 100%; background-color: #25282a; z-index: 2; }

.phone__list > li { margin-bottom: 6px; }

.phone__list > li:last-child { margin-bottom: 0; }

.phone__number { display: inline-flex; font-weight: 900; font-size: 24px; line-height: 29px; }

.phone__number > img { margin-right: 10px; width: 22px; }

.phone .arrow-down { position: relative; top: -12px; margin-left: 9px; width: 10px; }

.menu { margin-top: 26px; }

.menu__list { display: flex; align-items: center; justify-content: space-between; font-size: 16px; line-height: 19px; }

.menu__list > li { position: relative; }

.menu__list > li a { display: inline-block; position: relative; color: #ebebeb; }

.menu__list > li a:hover { color: #FF9300; }

.menu__list > li a.active { color: #FA4616; }

.menu__list > li .counter { position: absolute; top: 0; right: 0; font-weight: 300; font-size: 10px; line-height: 12px; padding: 1px 4px 1.5px; background-color: #ff1b1b; border-radius: 6.5px; -webkit-border-radius: 6.5px; -moz-border-radius: 6.5px; -ms-border-radius: 6.5px; -o-border-radius: 6.5px; transform: translate(50%, -40%); -webkit-transform: translate(50%, -40%); -moz-transform: translate(50%, -40%); -ms-transform: translate(50%, -40%); -o-transform: translate(50%, -40%); }

@media screen and (min-width: 1350px) { header .container { max-width: 1320px; } }

.first-screen { margin-top: -2px; height: 640px; background-color: #25282a; position: relative; color: #fff; overflow: hidden; }

.first-screen__line { position: absolute; bottom: 0; left: 0; width: 16%; }

.first-screen__title { max-width: 800px; margin: 86px 0 17px; }

.first-screen__text { max-width: 458px; font-size: 18px; line-height: 22px; margin-bottom: 67px; }

.slider { position: relative; }

.slider__item { position: relative; width: 100%; height: 640px; }

.slider__item > img { width: 52%; object-fit: cover; position: absolute; bottom: 0; right: 0; z-index: -1; }

.slider__box-arrow { position: absolute; bottom: 66px; left: 0; right: 0; display: flex; align-items: center; color: #fff; }

.slider__box-arrow .slick-counter { color: #979797; font-size: 18px; line-height: 22px; font-weight: 100; bottom: 0; right: 0; }

.slider__box-arrow .slick-counter .total::before { content: "/"; font-size: 36px; line-height: 43px; margin-left: 6px; }

.slider__box-arrow .slick-counter .current { font-size: 36px; line-height: 43px; color: #fff; }

.slider__next, .slider__prev { padding: 0; width: 20px; height: 10px; transition: width 0.4s ease; -webkit-transition: width 0.4s ease; -moz-transition: width 0.4s ease; -ms-transition: width 0.4s ease; -o-transition: width 0.4s ease; }

.slider__next { background: url(../image/icons/arrow-left.svg) no-repeat center center; transform: rotate(180deg); -webkit-transform: rotate(180deg); -moz-transform: rotate(180deg); -ms-transform: rotate(180deg); -o-transform: rotate(180deg); margin-left: 16px; }

.slider__next:focus, .slider__next:hover { width: 52px; background: url(../image/icons/arrow-right.svg) no-repeat center center; transform: rotate(0); -webkit-transform: rotate(0); -moz-transform: rotate(0); -ms-transform: rotate(0); -o-transform: rotate(0); }

.slider__prev { background: url(../image/icons/arrow-left.svg) no-repeat center center; margin-right: 16px; }

.slider__prev:focus, .slider__prev:hover { width: 52px; background: url(../image/icons/arrow-right.svg) no-repeat center center; transform: rotate(180deg); -webkit-transform: rotate(180deg); -moz-transform: rotate(180deg); -ms-transform: rotate(180deg); -o-transform: rotate(180deg); }

.our-account { padding: 110px 0 88px; }

