.slider-sale__item { padding: 0; }

.slider-sale__item .wrrap { flex: 0 0 59.9%; padding: 75px 48px; }

.slider-sale__image { height: 400px; flex: 0 0 40.1%; }

.calendar-section { padding: 125px 0; }

.calendar-section__title { font-weight: 900; font-size: 36px; line-height: 43px; color: #000; margin-bottom: 16px; width: 65%; }

.calendar-section__title + p { width: 55%; }

.calendar-section__block { margin-top: 35px; display: flex; }

.calendar-section__form { flex: 0 0 51.2%; padding: 40px 75px 50px 48px; }

