.calendar { margin-bottom: 40px; background: transparent; }

.calendar__header { margin-bottom: 41px; text-align: left; font-weight: 700; }

.calendar__header > p { position: relative; display: inline-block; color: #777777; margin-left: 20px; }

.calendar__header > p.active { color: #000; }

.calendar__header > p.active::after { content: ''; display: block; border-top: 3px solid #FF9300; position: absolute; bottom: -6px; left: 0; right: 0; }

.calendar > ul { display: flex; flex-wrap: wrap; }

